*{
    background-color: rgb(31, 46, 55);
    color: aliceblue;
}
.mainDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.heading{
    color:rgb(191, 225, 246);
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}
.heading h2{
    font-size: 80px;
}
.heading h1{
    margin-top: -20px;
    font-size: 180px;
}

.Auth{  
    border-radius: 10px;
    border-right: solid rgb(55, 104, 135);
    border-left: solid rgb(138, 206, 248);
    border-top: solid rgb(138, 206, 248);
    border-bottom: solid rgb(55, 104, 135);
    background-color: transparent;
    backdrop-filter: blur(10px);
    margin-top: 2%;
}

@media (max-width: 767px) {
    .heading  h2{
        margin-top: 30px;
        font-size: 60px;
    }
    .heading h1{
        font-size: 90px;
    }

    .Auth{
        margin-top: 30px;
        width: 350px;
        overflow: hidden;
    }


    
}